.hero {
	height: 0;
	min-height: 380px;
	max-height: 800px;
	margin-bottom:55px;
	background: url("/assets/images/stclairriver.jpg") no-repeat center center;
	background-size: cover;
	text-align: center;
}

.video-container {
	position: relative;
	padding-bottom: 56.25%;
	padding-top: 30px; height: 0; overflow: hidden;
}

.video-container iframe,
.video-container object,
.video-container embed {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}
